const $ = require('jquery')
const slick = require('slick-carousel')

$('#slider-rate').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    dots: true,
    mobileFirst: true,
    responsive: [
        {
            breakpoint: 768,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1,
            }
        },
        {
            breakpoint: 1110,
            settings: 'unslick'
        }
    ]
})

$('#slider-connect').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    dots: true,
    mobileFirst: true,
    responsive: [
        {
            breakpoint: 768,
            settings: 'unslick'
        }
    ]
})

$('#slider-reviews').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    dots: true,
    mobileFirst: true,
    responsive: [
        {
            breakpoint: 768,
            settings: {
                slidesToShow: 2,
            }
        }
    ]
})

$('#trigger').on('click', () => {
    $('#nav').toggleClass('-translate-x-[50rem]')
    $('body').toggleClass('overflow-hidden')
})
